import { defHttp } from '/@/utils/http/axios';
import { BasicColumn } from '/@/components/Table';
import type { FormProps } from '/@/components/Form/src/types/form';
import { useUserStore } from '/@/store/modules/user';

export enum Api {
  PAGE_EXECUTION_URL = '/po/vehicle-execution/page',
  LIST_STATION_TREE_URL = '/baseset/common/list-orgStationTree',
  LIST_STATION_PERSON_URL = '/baseset/common/list-stationPerson',
  LIST_ORG_TREE = '/system/org/list-tree',
  CREATE_URL = '/po/vehicle/create',
  UPDATE_URL = '/po/vehicle/update',
  DELETE_URL = '/po/vehicle/delete',
  UPLOAD_FILE_URL = '/po/vehicle/upload-file',
  EXPORT_EXCEL_URL = '/po/vehicle/export-excel',
  GET_URL = '/po/vehicle/get',
  PAGE_URL = '/po/vehicle/page',
}

export enum RecordType {
  // 保险记录
  INSURANCE = 'insurance',
  // 年检记录
  ANNUAL_REVIEW = 'annual-review',
  // 保养记录
  MAINTENANCE = 'maintenance',
  // 维修记录
  REPAIR = 'repair',
}

// 获取安全检查记录
export function getVehicleExecutionPage(params) {
  return defHttp.get({ url: Api.PAGE_EXECUTION_URL, params });
}

// 获取电站树
export const getTreeList = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_TREE_URL,
      params,
    },
    {},
  );

// 获取电站下的组织人员
export const getStationPerson = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_PERSON_URL,
      params,
    },
    {},
  );

// 获取组织树
export const getOrgTree = (params) =>
  defHttp.post(
    {
      url: Api.LIST_ORG_TREE,
      params,
    },
    {},
  );

// 添加车辆信息
export const createData = (params) =>
  defHttp.post(
    {
      url: Api.CREATE_URL,
      params,
    },
    {},
  );

// 修改车辆信息
export const updateData = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_URL,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return createData(params);
  }
  return updateData(params);
};

// 删除车辆信息
export const deleteData = (params) =>
  defHttp.delete(
    {
      url: Api.DELETE_URL,
      params,
    },
    {},
  );

// 导出车辆信息
export const exportExcel = (params) =>
  defHttp.download(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
    },
    { isReturnNativeResponse: true },
  );

