function customDictRender({ column, record }) {
  return record[column.dataIndex + 'Text'];
}

export const getCommonColumns = (sorter) => {
  return [
    { title: '创建时间', dataIndex: 'createTime', width: 150, sorter: sorter, resizable: true },
    { title: '修改时间', dataIndex: 'updateTime', sorter: sorter, resizable: true },
  ];
};

export const getColumns = () => {
  const columns = [
    {
      title: '车辆编号',
      dataIndex: 'vehicleNo',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '所属公司',
      dataIndex: 'stationOrganName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '归属电站',
      dataIndex: 'stationName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '车牌号',
      dataIndex: 'plateNumber',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '车辆类型',
      dataIndex: 'vehicleType',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: customDictRender,
    },
    {
      title: '识别代码',
      dataIndex: 'identificationCode',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 200,
    },
    {
      title: '发动机号',
      dataIndex: 'engineCode',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 200,
    },
    {
      title: '品牌',
      dataIndex: 'brand',
      headAlign: 'center',
      align: 'left',
      resizable: true,
      width: 150,
    },
    {
      title: '型号',
      dataIndex: 'model',
      headAlign: 'center',
      align: 'left',
      resizable: true,
      width: 150,
    },
    {
      title: '燃油类型',
      dataIndex: 'fuelType',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: customDictRender,
    },
    {
      title: '排量(L)',
      dataIndex: 'displacement',
      headAlign: 'center',
      align: 'right',
      sorter: true,
      resizable: true,
      width: 120,
    },
    {
      title: '车价(元)',
      dataIndex: 'price',
      headAlign: 'center',
      align: 'right',
      sorter: true,
      resizable: true,
      width: 120,
      format: { type: 'Money', format: '#,###.00' },
    },
  ];
  let result;
  if (useUserStore().isMultiOrganization) {
    result = columns as BasicColumn[];
  } else {
    const [one, , ...others] = columns;
    result = [one, ...others] as BasicColumn[];
  }
  return result.concat(getCommonColumns(true) as any);
};

// 保险记录
export const editRule = async (text) => {
  if (!text) {
    return '不能为空';
  }
  return '';
};
